import { navigate } from "gatsby";
import React, { useEffect } from "react";
import { useAuth0 } from "@auth0/auth0-react";
import { FaHome, FaInfoCircle, FaImages, FaAddressBook, FaUserAlt } from "react-icons/fa";
import LoginButton from "./LoginButton";
import LogoutButton from "./LogoutButton";

const linkItem = "flex flex-col items-center w-[100%] p-4 hover:cursor-pointer";
const selectedItem = "bg-zinc-100 text-zinc-900";
const heading = "mt-2"
const hover = 'hover:bg-zinc-950';

const Navbar = (): JSX.Element => {
  const [selected, setSelected] = React.useState(0);
  const { isAuthenticated, user } = useAuth0();

  if (isAuthenticated)
    console.log('user in nav', user);

  return (
    <div className="min-h-[100vh] bg-zinc-900 w-[165px] fixed left-0 top-0">
      <div className="flex flex-col min-h-[100vh]">
        <a className={`${linkItem}  ${selected == 0 ? selectedItem : hover}`} onClick={() => {
          setSelected(0)
          navigate("/")
        }}>
          <FaHome size={'30px'} />
          <p className={heading}>Home</p>
        </a>
        <a className={`${linkItem}  ${selected == 1 ? selectedItem : hover}`} onClick={() => {
          setSelected(1)
          navigate("/#about")
        }}>
          <FaInfoCircle size={'30px'} />
          <p className={heading}>About</p>
        </a>
        <a className={`${linkItem}  ${selected == 2 ? selectedItem : hover}`} onClick={() => {
          setSelected(2)
          navigate("/#photos")
        }}>
          <FaImages size={'30px'} />
          <p className={heading}>Photos</p>
        </a>
        <a className={`${linkItem}  ${selected == 3 ? selectedItem : hover}`} onClick={() => {
          setSelected(3)
          navigate("/#contact")
        }}>
          <FaAddressBook size={'30px'} />
          <p className={heading}>Contact</p>
        </a>
        {isAuthenticated &&
          <a className={`${linkItem}  ${selected == 4 ? selectedItem : hover} mt-auto`} onClick={() => {
            setSelected(4)
            navigate("/account")
          }}>
            <FaUserAlt size={'30px'} />
            <p className={heading}>Account</p>
          </a>
        }
        {!isAuthenticated &&
          <LoginButton />
        }{isAuthenticated &&
          <LogoutButton />
        }
      </div>
    </div>
  );
};

export default Navbar;
